'use client'

import Link from 'next/link'
import { useEffect, useState } from 'react'
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"

interface Customer {
  id: string
  name: string
  email: string
  phone: string
  address: string
  postalCode: string
  city: string
}

interface Subscription {
  id: string
  customerId: string
  status: 'active' | 'pending' | 'ended'
  startDate: string
  endDate: string
  price: number
  billingCycle: 'monthly' | 'quarterly' | 'yearly'
  serviceType: string
  address: string
  autoRenew: boolean
}

export default function Kunder() {
  const [customers, setCustomers] = useState<Customer[]>([])
  const [subscriptions, setSubscriptions] = useState<Subscription[]>([
    {
      id: '1',
      customerId: '1',
      status: 'active',
      startDate: '2024-03-31',
      endDate: '2025-03-31',
      price: 1200,
      billingCycle: 'monthly',
      serviceType: 'Standard vedlikehold',
      address: 'Testveien 1, 5020 Bergen',
      autoRenew: true
    }
  ])

  useEffect(() => {
    const savedCustomers = localStorage.getItem('customers')
    if (savedCustomers) {
      setCustomers(JSON.parse(savedCustomers))
    }
  }, [])

  return (
    <div className="container mx-auto p-4">
      <h1 className="text-2xl font-bold mb-6">Kundehåndtering</h1>
      
      <Tabs defaultValue="subscriptions" className="w-full">
        <TabsList>
          <TabsTrigger value="subscriptions">Abonnementer</TabsTrigger>
          <TabsTrigger value="customers">Kunder</TabsTrigger>
        </TabsList>

        <TabsContent value="subscriptions">
          <div className="bg-white rounded-lg shadow-lg p-6">
            <div className="flex justify-between items-center mb-6">
              <h2 className="text-xl font-semibold">Abonnementsoversikt</h2>
              <button className="bg-blue-600 text-white px-4 py-2 rounded-lg hover:bg-blue-700">
                Nytt abonnement
              </button>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-6">
              <div className="bg-green-100 p-4 rounded-lg">
                <h3 className="font-semibold">Aktive abonnementer</h3>
                <p className="text-2xl">{subscriptions.filter(s => s.status === 'active').length}</p>
              </div>
              <div className="bg-yellow-100 p-4 rounded-lg">
                <h3 className="font-semibold">Kommende abonnementer</h3>
                <p className="text-2xl">{subscriptions.filter(s => s.status === 'pending').length}</p>
              </div>
              <div className="bg-gray-100 p-4 rounded-lg">
                <h3 className="font-semibold">Avsluttede abonnementer</h3>
                <p className="text-2xl">{subscriptions.filter(s => s.status === 'ended').length}</p>
              </div>
            </div>

            <div className="overflow-x-auto">
              <table className="w-full">
                <thead className="bg-gray-50">
                  <tr>
                    <th className="px-4 py-2 text-left">Kunde</th>
                    <th className="px-4 py-2 text-left">Type</th>
                    <th className="px-4 py-2 text-left">Status</th>
                    <th className="px-4 py-2 text-left">Pris</th>
                    <th className="px-4 py-2 text-left">Fakturering</th>
                    <th className="px-4 py-2 text-left">Adresse</th>
                    <th className="px-4 py-2 text-left">Handling</th>
                  </tr>
                </thead>
                <tbody>
                  {subscriptions.map((sub) => (
                    <tr key={sub.id} className="border-b">
                      <td className="px-4 py-2">Kunde {sub.customerId}</td>
                      <td className="px-4 py-2">{sub.serviceType}</td>
                      <td className="px-4 py-2">
                        <span className={`px-2 py-1 rounded-full text-sm ${
                          sub.status === 'active' ? 'bg-green-100 text-green-800' :
                          sub.status === 'pending' ? 'bg-yellow-100 text-yellow-800' :
                          'bg-gray-100 text-gray-800'
                        }`}>
                          {sub.status === 'active' ? 'Aktiv' :
                           sub.status === 'pending' ? 'Kommende' : 'Avsluttet'}
                        </span>
                      </td>
                      <td className="px-4 py-2">{sub.price} kr</td>
                      <td className="px-4 py-2">{
                        sub.billingCycle === 'monthly' ? 'Månedlig' :
                        sub.billingCycle === 'quarterly' ? 'Kvartalsvis' : 'Årlig'
                      }</td>
                      <td className="px-4 py-2">{sub.address}</td>
                      <td className="px-4 py-2">
                        <button className="text-blue-600 hover:text-blue-800 mr-2">Rediger</button>
                        <button className="text-red-600 hover:text-red-800">Avslutt</button>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </div>
        </TabsContent>

        <TabsContent value="customers">
          <div className="flex justify-between items-center mb-6">
            <h1 className="text-2xl font-bold">Kunder</h1>
            <Link href="/kunder/ny">
              <Button>Ny kunde</Button>
            </Link>
          </div>

          <div className="grid gap-4">
            {customers.map((customer) => (
              <Card key={customer.id} className="p-4">
                <div className="flex justify-between items-start">
                  <div>
                    <h2 className="font-semibold">{customer.name}</h2>
                    <p className="text-sm text-gray-600">{customer.email}</p>
                    <p className="text-sm text-gray-600">{customer.phone}</p>
                    <p className="text-sm text-gray-600">
                      {customer.address}, {customer.postalCode} {customer.city}
                    </p>
                  </div>
                </div>
              </Card>
            ))}

            {customers.length === 0 && (
              <p className="text-center text-gray-500 py-8">
                Ingen kunder registrert ennå
              </p>
            )}
          </div>
        </TabsContent>
      </Tabs>
    </div>
  )
} 